import './App.css';
import React, {useState} from 'react';


function App() {
  
  const [numberShow, setNumberShow] = useState ("")
  const [total, setTotal] = useState (0)

  
  const displayNumber = (symbol)=>{
      setNumberShow(prev=> prev + symbol)
   
  }
  const calculate = () => {
    setTotal (eval(numberShow)  );
  }
 
 
  const restart = () =>{
    setTotal(0)
    setNumberShow('')
  }
  const clean = () =>{
    setNumberShow(prev => prev.split ("").slice(0,prev.length-1).join(""))
    setTotal(0)
  }

  return (
    <div className="container">
      <div className="grid">
        <div  className="screen">
            <input type="text" value={numberShow} placeholder='0' disabled  />
            <div className="resultOperation">{total}</div>
        </div>


        <div onClick={restart} className="padButton AC red-button">AC</div>
        <div onClick={clean} className="padButton C red-button">C</div>
        <div onClick={()=>displayNumber('/')} className="padButton divide">/</div>
        <div onClick={()=>displayNumber('*')} className="padButton multiply">X</div>
        <div onClick={()=>displayNumber('7')} className="padButton seven dark-number">7</div>
        <div onClick={()=>displayNumber('8')} className="padButton eight dark-number">8</div>
        <div onClick={()=>displayNumber('9')} className="padButton nine dark-number">9</div>
        <div onClick={()=>displayNumber('-')} className="padButton minus">-</div>
        <div onClick={()=>displayNumber('4')} className="padButton four dark-number">4</div>
        <div onClick={()=>displayNumber('5')} className="padButton five dark-number">5</div>
        <div onClick={()=>displayNumber('6')} className="padButton six dark-number">6</div>
        <div onClick={()=>displayNumber('+')} className="padButton add">+</div>
        <div onClick={()=>displayNumber('1')} className="padButton one dark-number">1</div>
        <div onClick={()=>displayNumber('2')} className="padButton two dark-number">2</div>
        <div onClick={()=>displayNumber('3')} className="padButton three dark-number">3</div>
        <div onClick={calculate} className="padButton equal blueButton">=</div>
        <div onClick={()=>displayNumber('0')} className="padButton zero dark-number">0</div>
        <div onClick={()=>displayNumber('.')} className="padButton point dark-number">.</div>
        
      </div>
    
    </div>
  );
}

export default App;
